@import '../config';
.slider-pickers-container {
    position: absolute;
    padding: @slider-padding * 2 @slider-padding;
    display: block;
    top: -@slider-padding + (@slider-bar-height / 2);
    left: 0;
    width: 99%;
    .slider-picker-inner {
        position: relative;
        display: block;
        width: 100%;

        .slider-picker-left {
            position: absolute;
            width: 0px;
            height: 0px;
            background-color: @highlight-color;
            border: 1px solid #FFF;
            box-shadow: 0 2px 1px #CCC;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            top: 0;
            z-index: 10;
            cursor: pointer;
            &:before {
                font-family: icomoon !important;
                font-style: normal !important;
                font-weight: normal !important;
                font-variant: normal !important;
                text-transform: none !important;
                line-height: 0;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                //content: '';
                font-size: 35px;
                background-color: white;
                border-radius: 35px;
                margin-left: -8px;
                margin-top: 30px;
                cursor: pointer;
                color: #258900;


              }
        }


        .slider-picker-right {
            position: absolute;
            width: 0px;
            height: 0px;
            background-color: @highlight-color;
            border: 1px solid #FFF;
            box-shadow: 0 2px 1px #CCC;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            top: 0;
            z-index: 10;
            cursor: pointer;
            &:before {
                font-family: icomoon !important;
                font-style: normal !important;
                font-weight: normal !important;
                font-variant: normal !important;
                text-transform: none !important;
                line-height: 0;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                //content: '';
                font-size: 35px;
                background-color: white;
                border-radius: 35px;
                margin-left: -8px;
                margin-top: 30px;
                cursor: pointer;
                color: #258900;
              }
        }




    }
}
